function comments(state = [],  action) {
  console.log('dispatch to postcommetns reducer');;
  console.log(action.comment);
  switch (action.type) {
    case 'ADD_COMMENT':
      // return existing state with the new comment
      return [
        ...state,
        action.comment
      ];
    default:
      return state;
  }
  return state;
}

export default comments;



